<template>
<div class='mainPage'>
    <my-menu />
    <life />
    <container v-for="(item, index) in containerList" :key="index" :topList='containerList[index]' />
    <m-nav />
</div>
</template>

<script>
import myMenu from '@/components/page/menu.vue'
import life from '@/components/page/life.vue'
import container from '@/components/page/container.vue'
import mNav from '@/components/page/nav.vue'
export default {
    data() {
        return {
            containerList: [
                {
                    title: '有格调',
                    class: 'iStyle',
                    children: ['全部', '聚会聚餐', '丽人SPA', '电影演出', '品质旅游']
                },
                {
                    title: '猫眼电影',
                    class: 'movie',
                    children: ['正在热映', '即将上映']
                },
                {
                    title: '推荐民宿',
                    class: 'hotal',
                    children: ['天津', '济南', '太原', '大连', '石家庄', '潍坊', '秦皇岛', '泰安', '呼和浩特', '大同']
                },
                {
                    title: '猜你喜欢',
                    class: 'like',
                    children: ['为你甄选最合适的']
                },
            ]
        }
    },
    components: {
        myMenu,
        life,
        container,
        mNav: mNav
    }
}
</script>

<style scope>

</style>


