<template>
<div class='searchBar'>
    <el-row class="contain">
        <el-col :span='6' class='left'>
            <img src="@/assets/images/logo.png" alt="">
        </el-col>
        <el-col :span='18' class='center'>
            <div class="wrapper">
                <el-input class="search-input" v-model="searchWord" placeholder="请输入内容" @focus="onfocus" @blur="onblur" @input='input'></el-input>
                <el-button type="primary" icon='el-icon-search'></el-button>
                <ul class="hotSearch list" v-if="isHotSearch">
                    <li class="hot">热门搜索</li>
                    <li>
                        <router-link tag="a" :to="{name: 'goodsList', params: {name: item}}" class="hotSearchWord" href="#" v-for="item in suggestList" :key='item'>{{item}}</router-link>
                    </li>
                </ul>
                <ul class="searchList list" v-if="isSearchList">
                    <router-link tag="li" class="searchListItem" :to="{name: 'goodsList', params: {name: item}}" v-for="(item, index) in searchList" :key='item + index'>
                        {{item}}
                    </router-link>
                </ul>
            </div>
            <p class="suggest">
                <router-link tag="span" :to="{name: 'goodsList', params: {name: item}}" class='suggest-item' v-for="item in suggestList" :key="item" href="#">{{item}}</router-link>
            </p>
        </el-col>
    </el-row>
</div>
</template>

<script>
import axios from '@/api/index.js';
export default {
  data () {
    return {
      searchWord: '',
      suggestList: [],
      searchList: [],
      isFocus: false,
    }
  },
  created() {
    axios.searchHotWords().then((res) => {
      this.suggestList = res.data.data
    })
  },
  computed: {
    isHotSearch () {
      return !this.searchWord && this.isFocus
    },
    isSearchList () {
      return this.searchWord && this.isFocus
    }
  },
  methods: {
    onfocus () {
      this.isFocus = true
    },
    onblur () {
      setTimeout(() => {
        this.isFocus = false
      }, 1000)
    },
    input() {
      axios.search().then((res) => {
      this.searchList = res.data.data.list.filter((item) =>{
        return item.indexOf(this.searchWord) != -1
      })
    })
    }
  }
}
</script>

<style lang='scss'>
   @import "@/assets/css/header/searchBar.scss";

</style>



