                    name: '侬的生煎·捞菜',
                    evaluationNum: 2435,
                    evaluationScore: 4.5,
                    address: '闽江道',
                    price: '6.8'
                },
            ]
        }
    }
}
</script>

<style lang='scss'>
    @import '@/assets/css/goodsList/guessLike.scss'
</style>


