<template>
<div class='changeCityPage'>
    <el-row class='changeByprovinces'>
        <p>按省份选择:</p>
        <citySelect class="citySelect" :title='provinceTitle' :itemList="provinceList" :isActive='provinceActive' @change_active='changeProvinceActive' />
        <citySelect class="citySelect" :title='cityTitle' :itemList="cityList" :isActive='cityActive' @change_active='changeCityActive' />
        <p>直接搜索:</p>
        <searchBox />
    </el-row>
    <el-row>
        <hotCity :title='hotCity' :cityList='hotCityList' />
    </el-row>
    <el-row>
        <hotCity :title='recentSearch' :cityList='recentSearchList' />
    </el-row>
    <el-row>
        <all-city />
    </el-row>
</div>
</template>

<script>
import citySelect from '@/components/changeCity/select.vue'
import searchBox from '@/components/changeCity/searchBox.vue'
import hotCity from '@/components/changeCity/hotCity.vue'
import allCity from '@/components/changeCity/allCity.vue'
export default {
    data() {
        return {
            hotCity: '热门城市',
            hotCityList: ['北京', '上海', '广州', '深圳', '天津', '西安', '重庆', '杭州', '南京', '武汉', '成都'],
            recentSearch: '最近访问',
            recentSearchList: ['北京', '上海', '广州', '深圳', '天津', '西安', '重庆', '杭州', '南京', '武汉', '成都'],
            provinceTitle: '省份',
            cityTitle: '城市',
            provinceActive: false,
            cityActive: false,
            provinceList: ['1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111'],
            cityList: ['1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111'],
        }
    },
    components: {
        citySelect,
        searchBox,
        hotCity,
        allCity
    },
    methods: {
        changeProvinceActive(flag) {
            this.provinceActive = flag;
            if (flag) {
                this.cityActive = false;
            }
        },
        changeCityActive(flag) {
            this.cityActive = flag;
            if (flag) {
                this.provinceActive = false;
            }
        }
    }
}
</script>

<style lang='scss'>
    @import '@/assets/css/changeCity/changeCity.scss'
</style>


