<template>
<div class='menu'>
    <div class="menu-content">
        <ul class='left' @mouseleave="menuLeave">
            <div class="all">全部分类</div>
            <template v-for="(item, index) in menuList">
                <li :key="index" @mouseenter="menuEnter(item)">
                    <i :class="item.icon"></i>
                    <span>{{item.name}}</span>
                    <span class="more">></span>
                </li>
            </template>
            <div v-if="curDetail" class="nav">
                <template v-for="(item, index) in curDetail.items">
                    <div class="title" :key="index">
                        <h4>{{item.title}}</h4>
                        <a>更多></a>
                    </div>

                    <a class="subtitle" href="#" v-for="(v, i) in item.items" :key="v + '-' + i">{{v}}</a>
                </template>
            </div>
            
        </ul>
        <ul class="right">
            <router-link to='/' tag='li'>美团外卖</router-link>
            <router-link to='/' tag='li'>猫眼电影</router-link>
            <router-link to='/' tag='li'>美团酒店</router-link>
            <router-link to='/' tag='li'>民宿/公寓</router-link>
            <router-link to='/' tag='li'>商家入驻</router-link>
            <router-link to='/' tag='li'>美团酒店</router-link>
        </ul>
    </div>
</div>
</template>

<script>
import axios from '@/api/index.js';
export default {
    data() {
        return {
            curDetail: null,
            menuList: []
        }
    },
    created() {
        axios.nav().then((res) => {
            this.menuList = res.data.data;
        })
    },
    methods: {
        menuEnter(item) {
            this.curDetail = item;
        },
        menuLeave() {
            this.curDetail = null;
        }
    }
}
</script>

<style lang="sass">
    @import '@/assets/css/page/menu.scss'
</style>




