<template>
<div class='login'>
    <el-row class="top">
        <div class="content">
            <img src="@/assets/images/logo.png" alt="">
        </div>
    </el-row>
    <el-row class="contain">
        <div class="content">
            <img src="https://s0.meituan.net/bs/file/?f=fe-sso-fs:build/page/static/banner/www.jpg" alt="">
            <div class="loginBox">
                <div class="warning" v-if='warning'>
                    <i class='el-icon-remove'></i>
                    <span>{{warning}}</span>
                </div>
                <p>
                    <span>账号登录</span>
                    <span class='loginByPhone'>手机动态码登录
                        <i class='el-icon-mobile-phone'></i>
                    </span>
                </p>
                <el-input
                    placeholder="手机号/用户名/邮箱"
                    prefix-icon="el-icon-search"
                    v-model="userId">
                </el-input>
                <el-input
                    placeholder="密码"
                    prefix-icon="el-icon-search"
                    type="password"
                    v-model="userPassword">
                </el-input>
                <router-link tag='span' to="/w">忘记密码?</router-link>
                <el-button type="primary" @click='loginClick'>登录</el-button>
                <p class="register">还没有账号?
                    <router-link tag="span" :to="{name: 'register'}">免费注册</router-link>
                </p>
                <div class="friendLink">
                    <p>用合作网站账号登录</p>
                    <a href="#" class='qq'></a>
                    <a href="#" class='wb'></a>
                </div>
            </div>
        </div>
    </el-row>
    <el-row class="bottom1">
        <div class="content">
            <a href="#" v-for="(item, index) in bottomList" :key="index">{{item}}</a>
        </div>
    </el-row>
    <el-row class="bottom2">
        <div class="content">
            ©2019 美团网团购 meituan.com 京ICP证070791号 京公网安备11010502025545号
        </div>
    </el-row>
</div>
</template>

<script>
import axios from '@/api/index.js'
export default {
    data() {
        return {
            userId: '',
            userPassword: '',
            warning: '',
            bottomList: ['关于美团', '加入我们', '商家入驻', '帮助中心', '美团手机版']
        }
    },
    methods: {
        loginClick() {
            if (!this.userId && !this.userPassword) {
                this.warning = '请输入用户名和密码';
            } else if (!this.userId) {
                this.warning = '请输入用户名';
            } else if (!this.userPassword) {
                this.warning = '请输入密码';
            }
            if (!this.warning) {
                axios.login({
                    params: {
                        userName: this.userId,
                        password: this.userPassword,
                    }
                }).then((res) => {
                    if (res.data.status == 'success') {
                        this.$router.push({name: 'myDefault'})
                        this.$store.commit('changeUserName', this.userId)
                    }
                })
                //
            }
        }
    }
}
</script>

<style lang='scss'>
    @import '@/assets/css/blank/login.scss'
</style>


