<template>
<div class='hotCity'>
    <dl :class='{"isLetter": isLetter}' v-if='isLetter'>
        <dt>{{title}}:</dt>
        <a :href="'#city-' + item" v-for='(item) in cityList' :key='item'>{{item}}</a>
    </dl>
    <dl v-else>
        <dt>{{title}}:</dt>
        <dd v-for='(item) in cityList' :key='item' @click="cityClick(item)">{{item}}</dd>
    </dl>
</div>
</template>

<script>
export default {
    props: ['cityList', 'title'],
    data() {
        return {
            isLetter: this.title == '按拼音首字母选择'
        }
    },
    // created() {
    //     console.log(this.isLetter)
    // }
    methods: {
        cityClick(item) {
            this.$store.commit('setPosition', item);
            this.$router.push({name: 'myDefault'})
        }
    }
}
</script>

<style lang='scss'>
    @import '@/assets/css/changeCity/hotCity.scss'
</style>


