import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import myDefault from '@/views/default'
import blank from '@/views/blank'
import page from '@/components/page/page.vue'
import changeCity from '@/components/changeCity/changeCity.vue'
import userCenter from '@/components/userCenter/index.vue'
import goodsList from '@/components/goodsList/index.vue'
import login from '@/components/blank/login.vue'
import register from '@/components/blank/register.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'myDefault',
      component: myDefault,
      redirect: '/index',
      children: [
        {
          path: 'index',
          name: 'mianPage',
          component: page
        },
        {
          path: 'changeCity',
          name: 'changeCity',
          component: changeCity
        },
        {
          path: 'goodsList/:name',
          name: 'goodsList',
          component: goodsList
        },
        {
          path: 'userCenter',
          name: 'userCenter',
          component: userCenter
        }
      ]
    },
    {
      path: '/blank',
      name: 'blank',
      component: blank,
      children: [
        {
          path: 'login',
          name: 'login',
          component: login
        },
        {
          path: 'register',
          name: 'register',
          component: register
        }
      ]
    }
  ]
})



