<template>
<div class='select'>
    <div class="selectBox" @click="selectClick" v-document-click='documentClick'>
        <p>{{title}}</p>
        <i class='el-icon-caret-bottom'></i>
    </div>
    <ul :class="{'itemBox': true, 'active': active}">
        <div class="title">{{title}}</div>
        <div v-for="(item, index) in itemColList" :key="index" class="item-col">
            <li v-for="(v, i) in item" :key='v + "_" + i'>{{v}}</li>
        </div>
    </ul>
</div>
</template>

<script>
export default {
    props: ['itemList', 'title', 'isActive'],
    data() {
        return {
            // isActive: false,
            itemColList: [],
            active: this.isActive,
            // itemList: ['1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111','1111']
        }
    },
    watch: {
        isActive(){
            this.active = this.isActive
        }
    },
    created() {
        while (this.itemList.length != 0) {
            if (this.itemList.length > 12) {
                this.itemColList.push(this.itemList.splice(0, 12));
            } else {
                this.itemColList.push(this.itemList);
                return
            }
        }
    },
    methods: {
        selectClick(e) {
            this.active = true;
            e.stopPropagation();
            this.$emit('change_active', true)
        },
        documentClick() {
            this.active = false;
            this.$emit('change_active', false)
        }
    }
}
</script>

<style lang='scss'>
    @import '@/assets/css/changeCity/select.scss'
</style>


