<template>
<div class='categroy'>
<!-- 类别 -->
    <dl>
        <dt>分类</dt>
        <dt>全部</dt>
        <dd v-for="(item, index) in classicList" :key='index'>
            <div>
                <span>{{item.title}}</span>
                <i class='el-icon-caret-bottom'></i>
                <dl class='list'>
                    <dt class='title'>{{item.title}}</dt>
                    <dd v-for='(v, i) in item.sub_list' :key='i'>{{v.name}}</dd>
                </dl>
            </div>
        </dd>
    </dl>
    <dl>
        <dt>区域</dt>
        <dt>全部</dt>
        <dd v-for='(item, index) in areaList' :key='index'>
          <div>
                <span>{{item.title}}</span>
                <i class='el-icon-caret-bottom'></i>
                <dl class='list'>
                    <dt class='title'>{{item.title}}</dt>
                    <dd v-for='(v, i) in item.sub_list' :key='i'>{{v.name}}</dd>
                </dl>
            </div>
        </dd>
    </dl>
</div>
</template>

<script>
export default {
data() {
    return {
      classicList: [
        {
          title: "美食",
          type: "food",
          sub_list: [
            {
              name: "日本菜",
              id: "Japan"
            },
            {
              name: "饮品店",
              id: "drink"
            },
            {
              name: "面包甜点",
              id: "cate"
            }
          ]
        },
        {
          title: "酒店住宿",
          type: "hotal",
          sub_list: [
            {
              name: "温泉酒店",
              id: "hot_spring"
            },
            {
              name: "经济型酒店",
              id: "economy"
            },
            {
              name: "高端酒店",
              id: "high"
            }
          ]
        }
      ],
      areaList: [
        {
          title: "推荐商圈",
          sub_list: [
            {
              name: "望京",

              id: 120000
            },

            {
              name: "昌平",

              id: 12222
            }
          ]
        }
      ]
    }
}
}
</script>

<style lang='scss'>
    @import '@/assets/css/goodsList/categroy.scss'
</style>


