<template>
<div class=''>
    <el-select
    v-model="searchWord"
    remote
    filterable
    reserve-keyword
    placeholder="请输入城市中文或拼音"
    :remote-method="remoteMethod"
    :loading="loading">
    <el-option
      v-for="item in searchList"
      :key="item"
      :label="item"
      :value="item">
      {{item}}
    </el-option>
  </el-select>
</div>
</template>

<script>
export default {
    data() {
        return {
            searchWord: '',
            loading: false,
            searchList: ['111','222','333','444','555','666','777']
        }
    },
    methods: {
        remoteMethod(val) {
            // 进行网络请求
        }

    }
}
</script>

<style scoped>

</style>


