<template>
<div class='goodsList'>
    <el-row class='curmb'>
        <crumb />
    </el-row>
    <el-row>
        <el-col :span='19'>
            <el-row class='categroyCmp'>
                <categroy />
            </el-row>
            <el-row class="commonList_">
                <common-list />
            </el-row>
            <el-row>
                <pagination />
            </el-row>
        </el-col>
        <el-col :span='5'>
            <guessLike />
        </el-col>
    </el-row>
    
</div>
</template>

<script>
import crumb from '@/components/goodsList/crumb.vue'
import categroy from '@/components/goodsList/categroy.vue'
import commonList from '@/components/goodsList/commonList.vue'
import guessLike from '@/components/goodsList/guessLike.vue'
import pagination from '@/components/goodsList/pagination.vue'
export default {
    components: {
        crumb,
        categroy,
        commonList,
        guessLike,
        pagination
    }
}
</script>

<style lang='scss'>
    @import '@/assets/css/goodsList/index.scss'
</style>



